<template>
  <div class="container">
	<my-header title="报案记录查询"></my-header>
	<section>
		<down-load ref='mescroll' @getDate="getDate">
	        <ul slot='listInfo' ref='scroll'>
	        	<li v-for="i in listData">
	        		<div>
	        			<p>
	        				<span>
	        					<i class="fa fa-id-card-o" aria-hidden="true"></i>
	        					<b>赔案号码：{{i.claimNo}}</b>
	        				</span>
	        				<!--<i class="fa fa-angle-right" aria-hidden="true"></i>-->
	        			</p>
	        			<ul>
	        				<li>
	        					<ul>
	        						<li>出险人姓名：<span>{{i.insuredName}}</span></li>
	        						<li>案发性质：<span>{{i.claimNature|valToText(claimApply.claimNature)}}</span></li>
	        					</ul>
	        				</li>
	        				<li>
	        					<ul>
	        						<li>事故日期：<span>{{i.accidentTime}}</span></li>
	        						<li>理赔类型：<span>{{i.claimType|valToText(claimApply.claimTypeOgg)}}</span></li>
	        					</ul>
	        				</li>
	        			</ul>
						<div>
							<span :class="(i.showType==='0'&&(i.claimType==='3'||i.claimType==='4'||i.claimType==='5'||i.claimType==='6'))?'orangeColor':''" @click="toInsurePeople(i)">申请理赔</span>
						</div>
	        		</div>
	        	</li>
	        </ul>
        </down-load>
	</section>
  </div>
</template>
<script>
import MyHeader from "@/components/model/MyHeader";
import DownLoad from '@/components/model/DownLoad';
import {RECORDREPORTINFO} from "@/api";
import {mapState} from 'vuex';
export default {
  name: 'RecordReport',
  data(){
  	return {
  		listData:[]
  	}
  },
  computed:{
	...mapState(['claimApply','claimTypeOgg','claimType','claimNature','claimtList'])	
  },
  beforeRouteEnter(to,from,next){
	next(vm => {
		//理赔申请页面进入重新刷新
		if(from.name==="Apply"){
			vm.listData=[];
	  		vm.$refs.mescroll.mescroll.resetUpScroll();
	  	}
		vm.$refs.scroll.offsetParent.scrollTop=sessionStorage.getItem("scrollTop")||0;
	})
  },
  beforeRouteLeave(to,from,next){
	sessionStorage.setItem("scrollTop",this.$refs.scroll.offsetParent.scrollTop);
	next()
  },
  methods:{
  	toInsurePeople(flag){
  		if(flag.showType==='0'&&(flag.claimType==='3'||flag.claimType==='4'||flag.claimType==='5'||flag.claimType==='6')){
  			sessionStorage.setItem("claimtList",JSON.stringify(flag));
	  		this.claimApply.claimtList=JSON.parse(sessionStorage.getItem("claimtList",JSON.stringify(flag)));
	  		this.$router.push({path:this.$router.history.current.path+"/insurepeople",query:{
				claimNo:flag.claimNo
			}});
  		}
  	},
  	 async getDate(page,mescroll){
  	 	try{
  	 		let arr = await RECORDREPORTINFO({
					...this.$route.query,
				"status":"1",
				"pageIndex":page.num+"",
				"pageSize":page.size+""
			});
	  		if (page.num === 1) this.listData = []
			this.listData = this.listData.concat(arr.data.data);
			this.$nextTick(()=>{
				mescroll.endSuccess(arr.data.data.length);
			})
 		}
 		catch(err){
 			this.$nextTick(()=>{
				mescroll.endSuccess(0);
			})
 		}
 		
  		
  	}
  },
  components:{
  	MyHeader,DownLoad
  }
}
</script>
<style lang="stylus" scoped>
.container{
	height:100%;
	overflow-y: auto;
    position: relative;
	display:flex;
	flex-direction: column;
	&>section{
		position: relative;
		flex:1;
		#noDate>ul>li{
			background:#fff;
			padding:10px 12px 16px;
			margin-bottom:10px;
			&>div{
				border:1px solid #ccc;
				&>p{
					display:flex;
					padding:0 7px;
					align-items: center;
    				justify-content: space-between;
					height:48px;
					line-height:49px;
					border-bottom:1px dotted #ccc;
					.fa-id-card-o{
						font-size:20px;
						margin-right:8px;
						color:#fd7f47;
					}
					.fa-angle-right{
						font-size:24px;
					}
				}
				&>ul{
					display:flex;
					padding:14px 0 0 7px;
					&>li{
						display:flex;
						flex:1;
						&>ul{
							display:flex;
							flex-direction:column;
							justify-content:space-between;
							&>li{
								margin-bottom:24px;
							}
						}
						
					}
				}
				&>div{
					text-align:center;
					&>span{
						padding:10px 40px;
						display: inline-block;
						border-radius:5px;
						margin-bottom:14px;
						color:#ccc;
						border:1px solid #ccc;
					}
					&>.orangeColor{
						color:#fd7f47;
						border:1px solid #fd7f47;
					}
				}
			}
		}
	}
}
</style>


